<template>
    <v-card
        class="mt-4 mb-5"
        width="80vw"
        style="border-radius: 10px;"
        height="70vh"
        hover
    >
        <v-toolbar color="primary">
            <v-toolbar-side-icon>
              <v-icon color="white">
                fas fa-map-marked-alt
              </v-icon>
            </v-toolbar-side-icon>
            <v-toolbar-title class="white--text">
                Path Travelled
            </v-toolbar-title>

            <v-spacer></v-spacer>

            <v-btn color='secondary'>
                <v-icon class="mr-2">
                    fas fa-file-export
                </v-icon>
                Export As PNG
            </v-btn>
        </v-toolbar>
       <GmapMap
            ref="map"
            :center="user.farmCenter"
            :zoom="19"
            map-type-id="satellite"
            style="width: 100%; height: 100%;"
        >
            <gmap-polyline
                v-if="paths.length > 0"
                :path="paths"
                :options="polyOptions"
            ></gmap-polyline>

        </GmapMap>
    </v-card>
</template>

<script>
import { mapState, mapActions } from 'vuex';

export default {
  name: 'GPSTracks',
  computed: {
    ...mapState([
      'user',
      'paddocks',
      'tsData',
    ]),

    paths() {
      const paths = [];
      const dataLen = this.tsData.length;
      for (let i = 0; i < dataLen; i += 1) {
        const coordinate = this.tsData[i].data.gps.split(',');
        paths.push({ lat: parseFloat(coordinate[0]), lng: parseFloat(coordinate[1]) });
      }

      return paths;
    },
  },
  data: () => ({
    polyOptions: {
      strokeColor: '#74B3CE',
    },
  }),
};
</script>
